body {
  background-image: linear-gradient(to right top, #2e2e2e, #756246);
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}
input {
  padding: 10px;
  width: 100%;
}
.lisays {
  width: 75vw;
}
form {
  display: flex;
  flex-wrap: nowrap;
}
.tehtavan-nimi {
  resize: none;
  background: transparent;
  border: 0;
  width: min-content;
  height: auto;
}
.lisays button {
  background: transparent;
  border: 1px solid black;
  outline: 1px solid black;
  padding: 10px;
  cursor: pointer;
  transition: 250ms outline-offset ease;
}

button:hover,
button:focus {
  outline-offset: -4px;
}
.tehtava {
  display: flex;
  justify-content: space-between;
  background: rgba(0, 0, 0, 0.2);
  border-bottom: 1px solid white;
}
.tehtava button {
  border: 0;
  background: transparent;
  padding: 15px;
  cursor: pointer;
  font-size: 20px;
}
.tehtava button:hover,
.tehtava button:focus {
  transform: scale(1.2);
  color: green;
}
